.landingHeader {
  width: 100%;
  min-height: 100vh;
  background: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.2)), url("/images/landing-header-background.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: top center;
  position: relative; }
  .landingHeader div {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) scale(0);
    transition: opacity 300ms, transform 1000ms;
    display: flex;
    flex-direction: column;
    align-items: center;
    opacity: 0; }
    .landingHeader div.animate {
      opacity: 1;
      transform: translate(-50%, -50%) scale(1); }
    .landingHeader div * {
      text-align: center; }
    .landingHeader div h1 {
      font-family: "Amatic SC", cursive;
      font-size: 64px;
      line-height: 1;
      color: #fff;
      max-width: 840px; }
    .landingHeader div h2 {
      font-family: 'Amatic SC', cursive;
      font-size: 50px;
      color: #fff;
      max-width: 740px; }
    .landingHeader div h4 {
      margin-top: 20px;
      font-family: 'Amatic SC', cursive;
      color: #fff;
      font-size: 24px;
      line-height: 1.4;
      max-width: 640px; }
    .landingHeader div a {
      margin-top: 20px;
      padding: 10px 30px;
      background: #dc3522;
      color: #f2f2f2;
      border: 1px solid #dc3522;
      border-radius: 30px;
      outline: none;
      transition: background 300ms linear, color 300ms linear, opacity 500ms;
      opacity: 0; }
      .landingHeader div a:hover {
        background: #ededed;
        color: #dc3522; }
      .landingHeader div a.animate {
        opacity: 1; }
    @media (max-width: 767px) {
      .landingHeader div h1 {
        font-size: 48px;
        line-height: 1; }
      .landingHeader div h4 {
        line-height: 1.2; } }
