.menuNav {
  z-index: 1;
  left: 0;
  bottom: 0;
  position: absolute;
  width: 100%;
  height: 54px;
  transition: all 300ms;
  display: flex;
  align-items: center; }
  .menuNav.fixed {
    position: fixed;
    background: #ffa500; }
  .menuNav * {
    overflow: hidden; }
  .menuNav .slider-wrapper {
    z-index: 2;
    display: flex;
    justify-content: center;
    height: 48px; }
    .menuNav .slider-wrapper .slider-inner {
      z-index: 4;
      background: #e1e1e1;
      position: relative;
      overflow-x: scroll;
      height: 70px;
      width: 640px; }
      .menuNav .slider-wrapper .slider-inner ul.slider {
        z-index: 5;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: 'flex-start';
        flex-wrap: nowrap;
        position: absolute;
        height: 70px;
        /* top: -11px; */
        top: -11px; }
        .menuNav .slider-wrapper .slider-inner ul.slider.center {
          left: 50%;
          transform: translateX(-50%); }
        .menuNav .slider-wrapper .slider-inner ul.slider li {
          margin-left: 6px;
          border-radius: 20px;
          height: 40px;
          padding: 2px 0;
          display: flex;
          justify-content: center;
          align-items: center; }
          .menuNav .slider-wrapper .slider-inner ul.slider li:last-child {
            margin-right: 6px; }
          .menuNav .slider-wrapper .slider-inner ul.slider li a {
            width: 100%;
            height: 100%;
            font-family: 'Leckerli One', cursive;
            /* font-family: 'Amatic SC', cursive; */
            font-size: 16px;
            /* font-weight: bold; */
            /* padding: 6px 8px; */
            padding: 0 10px;
            height: 40px;
            display: flex;
            align-items: center;
            border-radius: inherit;
            transition: all 300ms ease-in-out;
            white-space: nowrap;
            color: #8b0000; }
            .menuNav .slider-wrapper .slider-inner ul.slider li a:hover {
              color: #d80000; }
            .menuNav .slider-wrapper .slider-inner ul.slider li a.menu-nav-active {
              /* background: ${props => props.theme.lightRed}; */
              background: #8b0000;
              color: #ededed; }
