.menuContentBox {
  margin: 1rem auto; }
  .menuContentBox h1 {
    /* ${props => props.theme.fontLeckerli} */
    color: #232d3e;
    text-align: center;
    font-family: "Amatic SC", cursive;
    font-size: 64px;
    font-weight: 700;
    line-height: 1.4; }
    @media (max-width: 767px) {
      .menuContentBox h1 {
        font-size: 44px; } }
  .menuContentBox .itemsBox {
    margin-top: 10px;
    padding: 20px;
    /* background: rgba(0, 0, 0, 0.5); */ }
    @media (max-width: 767px) {
      .menuContentBox .itemsBox {
        padding: 10px; } }
    .menuContentBox .itemsBox .itemBoxGrid {
      display: grid;
      grid-gap: 20px;
      grid-template-columns: 1fr 1fr;
      /* grid-template-columns: repeat(auto-fit, minmax(400px, 1fr)); */ }
      @media (max-width: 767px) {
        .menuContentBox .itemsBox .itemBoxGrid {
          grid-gap: 10px;
          grid-template-columns: 1fr; } }
      .menuContentBox .itemsBox .itemBoxGrid .itemBox {
        /* background: rgba(255, 0, 0, 0.5); */
        display: flex;
        justify-content: center; }
        .menuContentBox .itemsBox .itemBoxGrid .itemBox .imageBox {
          overflow: hidden;
          width: 160px;
          height: 160px;
          border-radius: 80px;
          /* width: 200px;
          height: 140px;
          border-radius: 8px; */
          /* position: relative; */ }
          @media (max-width: 1023px) {
            .menuContentBox .itemsBox .itemBoxGrid .itemBox .imageBox {
              width: 140px;
              height: 140px;
              border-radius: 70px; } }
          .menuContentBox .itemsBox .itemBoxGrid .itemBox .imageBox img {
            /* position: absolute;
            left: 50%;
            transform: translateX(-50%); */
            -webkit-transform: translateZ(0);
            -webkit-perspective: 1000;
            -webkit-backface-visibility: hidden;
            width: 160px;
            height: 160px;
            border-radius: 80px;
            user-select: none; }
            @media (max-width: 767px) {
              .menuContentBox .itemsBox .itemBoxGrid .itemBox .imageBox img {
                width: 140px;
                height: 140px;
                border-radius: 70px; } }
        .menuContentBox .itemsBox .itemBoxGrid .itemBox .contentBox {
          flex: 1;
          margin-left: 20px;
          display: flex;
          flex-direction: column;
          justify-content: center; }
          @media (max-width: 767px) {
            .menuContentBox .itemsBox .itemBoxGrid .itemBox .contentBox {
              margin-left: 10px; } }
          .menuContentBox .itemsBox .itemBoxGrid .itemBox .contentBox .name {
            position: relative;
            font-family: "Roboto Slab", serif;
            color: #393939;
            font-weight: bold;
            font-size: 18px;
            line-height: 1.5; }
            .menuContentBox .itemsBox .itemBoxGrid .itemBox .contentBox .name .day {
              position: absolute;
              font-size: 12px;
              font-style: italic;
              margin-top: -12px;
              top: 0;
              color: green; }
              @media (max-width: 565px) {
                .menuContentBox .itemsBox .itemBoxGrid .itemBox .contentBox .name .day {
                  margin-top: -16px;
                  left: 30%; } }
          .menuContentBox .itemsBox .itemBoxGrid .itemBox .contentBox .price {
            font-size: 18px;
            font-weight: bold;
            font-family: "Playfair Display", serif;
            color: #a50000; }
            .menuContentBox .itemsBox .itemBoxGrid .itemBox .contentBox .price .striked {
              color: #8b0000;
              text-decoration: line-through; }
          .menuContentBox .itemsBox .itemBoxGrid .itemBox .contentBox .description {
            color: #3a3a3a;
            font-style: italic;
            line-height: 1.2; }
